/* Tabs */
/* Style the tab */

button {
  border: none;
  outline: none;
  background-color: transparent;
}

.tab {
  margin-top: 30px;
  overflow: hidden;
  background-color: #bce2f3;
  border: none;
  outline: none;
}

.tablinks {
  font-weight: bold;
  font-size: 16px;
}


/* Style the buttons that are used to open the tab content */

.tab button {
  background-color: inherit;
  float: left;
  margin-right: 23px;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: all 0.4s ease-in-out;
  border-bottom: 4px solid transparent;
}

.opened {
  display: block !important;
}

/* Change background color of buttons on hover */

.tab button:hover {
  background-color: #c2e3f1;
  border-bottom: 4px solid #385e8f;
}

/* Style the tab content */
.tabcontent {
  display: none;
  width: 100%;
  padding: 10px 22px;
  border: 1px solid #bce2f396;
  border-top: none;
  text-align: justify;
  text-indent: 3ch;
}

.btn_qabul {
  background-color: #337ab7;
  color: #fff !important;
}

/* Accordion */


/* Responsive */
@media (max-width: 767px) {
  .tab {
    display: flex;
    flex-wrap: wrap;
  }

  .tablinks:last-child {
    border-top: 1px solid #63c7f5ad;
    margin-top: 7px;
  }
}

.page_4 .row,
.page_main .row {
  justify-content: center;
}

.dropdown:hover>.dropdown-menu {
  display: block;
}

@media (max-width: 1500px) {
  .page_main ul li {
    width: 45%;
  }
}

@media (max-width: 1000px) {
  .page_main ul li {
    width: 80%;
  }
}

.pagination .page-num {
  background: linear-gradient(111.67deg, rgba(48, 126, 199, 0.911) 27.45%, rgba(129, 211, 236, 0.623) 228.39%) !important;
  color: #fff !important;
  border: 1.5px solid #fff;
}